import React, { Component } from 'react';
import Tooltip from "@mui/material/Tooltip";

class GalBladder extends Component {

    render() {
        return (
            <Tooltip title="Gall Bladder" placement="right">
                <svg
                    width="500"   // adjust size as needed
                    height="500"
                    viewBox="0 0 500 500"
                >
                    <path
                        id="gall_bladder"
                        className="gall_bladder"
                        fill="#808080"
                        fillOpacity="0.5"
                        stroke="#787878"
                        strokeWidth="0.5"
                        d="M214.75,231.5
                           c-2.462-0.055-8.877-1.733-10.175,1.517
                           c-1.906,4.77,7.269,4.012,9.553,3.994
                           c2.029-0.017,9.188,0.599,9.7-2.883
                           c0.47-3.208-7.819-2.605-9.827-2.628"
                    />
                </svg>
            </Tooltip>
        )
    }
}

export default GalBladder;
